import React from "react";
import { graphql } from "gatsby";
import Layout from "../Components/Layout";
import "../Styles/Global.css";
import Img from "gatsby-image";
import { SEO } from "theme-seo";

import { useRef } from 'react';
import Autoplay from 'embla-carousel-autoplay';
import { Carousel } from '@mantine/carousel';

const Page = ({ data, pageContext }) => {
  const faqs = pageContext.faqs;
  const autoplay = useRef(Autoplay({ delay: 3000 }));
  return (
    <Layout page={pageContext.pageName} faqs={faqs}>
      <SEO
        title={pageContext.title}
        seoTitle={pageContext.seoTitle}
        description={pageContext.description}
        imageUrl={data.file && data.file.publicURL}
        faqs={faqs}
      />

      <div className="WordpressStyles my-24 mx-auto">
        {data.allImages.nodes.length > 1 ? <div dir="ltr">
          <Carousel
            maw={"90%"}
            mx="auto"
            withIndicators
            height={"50%"}
            plugins={[autoplay.current]}
            onMouseEnter={autoplay.current.stop}
            onMouseLeave={autoplay.current.reset}

          >
            {data.allImages.nodes.map((img) =>
            (
              <Carousel.Slide >
                <img
                  style={{ height: "100%", width: "100%" }}
                  alt={img.name}
                  src={img.publicURL}
                />
              </Carousel.Slide>
            )
            )}
          </Carousel>
        </div> : data.file && data.file.extension !== "md" &&
        (data.file.extension === "svg" ? (
          <img
            style={{ width: "100%" }}
            width="1519"
            height="624"
            alt={data.file.name}
            src={data.file.publicURL}
          />
        ) : (
          <Img alt={data.file.name} fluid={data.file.childImageSharp.fluid} />
        ))
        }
        <div
          className="w-10/12 lg:w-4/5 mx-auto"
          dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}
        ></div>
      </div>
    </Layout>
  );
};

export default Page;

export const query = graphql`
  query($pageSlug: String, $pageName: String) {
    markdownRemark(fields: { slug: { regex: $pageSlug } }) {
      html
      fields {
        slug
      }
      frontmatter {
        title
        order
      }
    }

    allImages:  allFile(filter: { name: {regex: $pageSlug}, extension: { ne: "md" }, sourceInstanceName: { eq: "page" }}) {
      nodes {
        name
        publicURL
        extension
        childImageSharp {
          fluid(maxWidth: 2500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
    file(name: { eq: $pageName }, extension: { ne: "md" }) {
      name
      publicURL
      extension
      childImageSharp {
        fluid(maxWidth: 2500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
